import React, { Fragment } from "react";

import Box from "@material-ui/core/Box";
import InfoModal from "components/InfoModal";
import PropTypes from "prop-types";
import Slider from "@material-ui/core/Slider";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "90%",
        margin: "auto auto",
    },
    margin: {
        height: theme.spacing(3),
    },
    title: {
        display: "flex",
        alignItems: "center",
    },
}));

const marks = [
    {
        value: 1,
        label: " ",
    },
    {
        value: 2,
        label: " ",
    },
    {
        value: 3,
        label: " ",
    },
];

function valuetext(value) {
    return `${value}`;
}

export default function SchemaSlider({ onChange }) {
    const classes = useStyles();

    const modalTitle = "  ";
    const modalContent = (
        <Fragment>
            <Typography variant="body1" gutterBottom>
                   .      {" "}
                <Box component="span" fontWeight="fontWeightBold" fontStyle="italic">
                       
                </Box>
                ,{" "}
                <Box component="span" fontWeight="fontWeightBold" fontStyle="italic">
                         
                </Box>{" "}
                  .
            </Typography>
            <Typography gutterBottom variant="body1">
                        .            .     
                      .
            </Typography>
        </Fragment>
    );
    return (
        <div className={classes.root}>
            <Box className={classes.title}>
                <Typography display="block" variant="h5">
                      
                </Typography>
                <InfoModal title={modalTitle} content={modalContent} />
            </Box>
            <Slider min={1} max={3} defaultValue={2} getAriaValueText={valuetext} aria-labelledby="discrete-slider-always" marks={marks} step={null} valueLabelDisplay="off" onChange={onChange} />
        </div>
    );
}

SchemaSlider.propTypes = {
    onChange: PropTypes.func,
};
