import React, { Fragment } from "react";

import Paper from "@material-ui/core/Paper";
import ReactHelmet from "components/ReactHelmet";
import ShowcaseCard from "components/ShowcaseCard";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "grid",
        gridTemplateColumns: "repeat(auto-fit, minmax(275px, 1fr))",
        gap: theme.spacing(2),
        padding: 0,
    },
    wrapper: {
        padding: theme.spacing(5, 2),
    },
}));

// TODO: fix key warning
const Home = ({ title }) => {
    const classes = useStyles();

    const cards = [
        {
            title: " ",
            category: "",
            body: "  .    ,      .",
            action: "",
            to: "/portfolio",
        },
        {
            title: " ",
            category: "",
            body: "        .    ,    ,   ,  ,       .",
            action: "",
            to: "/kakao-analyser",
        },
        {
            title: "  ",
            category: "",
            body: " , , ,       .     IPIP-NEO()     .          .",
            action: "",
            to: "/character-maker",
        },
    ];

    const cardsComponent = cards.map((element, index) => {
        return <ShowcaseCard key={index} title={element.title} category={element.category} body={element.body} action={element.action} to={element.to} />;
    });
    return (
        <Fragment>
            <ReactHelmet description="    .     ,      ." keywords="BK927" />
            <Paper className={classes.wrapper} elevation={3}>
                <div className={classes.root}>{cardsComponent}</div>
            </Paper>
        </Fragment>
    );
};

export default Home;
